import { useState } from "react";
import { useDispatch } from "react-redux";
import shortid from "shortid";

import authOperations from "../../Redux/auth/auth-operations";
import s from "../PhoneBook.module.css";

export default function Login() {
  const dispatch = useDispatch();
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  const handleChange = ({ target: { name, value } }) => {
    switch (name) {
      case "email":
        return setEmail(value);
      case "password":
        return setPassword(value);

      default:
        return;
    }
  };

  const handleSubmite = (e) => {
    e.preventDefault();
    dispatch(authOperations.logIn({ email, password }));
    resetForm();
  };

  const resetForm = () => {
    setEmail("");
    setPassword("");
  };

  const idEmail = shortid.generate();
  const idPassword = shortid.generate();

  return (
    <div>
      <h1 className={s.headingForm}>Ð¡ÑÐ¾ÑÑÐ½ÐºÐ° Ð°Ð²ÑÐ¾ÑÐ¸Ð·Ð°ÑÑÑ</h1>
      <form className={s.form} action="" onSubmit={handleSubmite}>
        <label className={s.labelName} htmlFor={idEmail}>
          ÐÐ¾ÑÑÐ°
        </label>
        <input
          id={idEmail}
          value={email}
          name="email"
          type="email"
          onChange={handleChange}
        />
        <label className={s.labelName} htmlFor={idPassword}>
          ÐÐ°ÑÐ¾Ð»Ñ
        </label>
        <input
          id={idPassword}
          value={password}
          name="password"
          type="password"
          onChange={handleChange}
        />
        <button className={s.btnReg} type="submit">
          Ð£Ð²ÑÐ¹ÑÐ¸
        </button>
      </form>
    </div>
  );
}
